.text-block__wrapper {
	@include wrapper;
	display: flex;
	flex-direction: column;
}

.text-block__wrapper h2 {
	margin-bottom: 24px;
}

.text-block__text p {
	margin: 0;
	margin-bottom: 16px;
	font-size: 14px;
	line-height: 20px;
	text-align: center;
}

.text-block__button {
	@include button-reset;
	margin-bottom: 36px;
	justify-content: center;
	font-size: 16px;
	font-weight: 600;
	line-height: 24px;
	letter-spacing: 0em;
	color: #e97522;
}