@font-face {
  font-family: "Motserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Motserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Motserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium-Medium.woff") format("woff");
  font-display: swap;
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "FuturaLT";
  src: url("../fonts/FuturaLT-Bold.woff2") format("woff2"), url("../fonts/FuturaLT-Bold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 500;
  color: #a4a4a4;
  background-color: #ffffff;
  font-family: "Montserrat", "Arial", sans-serif;
  counter-reset: number 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  user-select: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  width: 100%;
  max-width: 1023px;
  /* margin: 0 auto; */
  padding-left: 16px;
  padding-right: 16px;
  margin: 0;
  margin-top: 8px;
  margin-bottom: 8px;
}
@media (min-width: 1024px) {
  .main-header {
    max-width: 1439px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    max-width: 1440px;
    padding-left: 130px;
    padding-right: 130px;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logo {
  margin-right: 60px;
}

.main-header__phone-button {
  display: none;
}

.main-header__phone-link {
  order: 1;
  margin-right: 24px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  display: flex;
  align-items: center;
}

.main-header__phone-img {
  margin-left: 20px;
}

.main-header__toggle {
  order: 2;
  padding: 0;
  border: none;
  background-color: #ffffff;
}

.main-nav {
  display: block;
  position: absolute;
  top: -100vw;
  left: 0;
  width: 100%;
  margin: 0;
  visibility: hidden;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.intro__wrapper {
  /* padding-left: 27px;
  padding-right: 27px; */
  position: relative;
  background-color: #444444;
}

.content__text {
  /* position: relative; */
  padding: 182px 27px 0 28px;
  position: absolute;
  z-index: 10;
  width: 100%;
}

.content__video {
  margin: 0;
}

.intro__description-title {
  margin: 0;
  display: none;
}

.intro__description-text {
  margin: 0;
  display: none;
}

.button {
  border: 0;
  padding: 0;
  background: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 19px 0;
  background: linear-gradient(180deg, #FFA96B -21.43%, #EE7A28 100%);
  border-radius: 10px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #ffffff;
  width: 100%;
}

.work {
  padding-top: 60px;
}

.work__toggles {
  display: flex;
  justify-content: center;
  margin-bottom: 60px;
}

.locations__wrapper {
  width: 100%;
  max-width: 1023px;
  /* margin: 0 auto; */
  padding-left: 16px;
  padding-right: 16px;
  margin-bottom: 62px;
}
@media (min-width: 1024px) {
  .locations__wrapper {
    max-width: 1439px;
    padding-left: 20px;
