.locations__slider-link--none {
  display: none;
}

.locations__slider-image {
  margin: 0 auto;
}

.services__wrapper {
  margin-bottom: 68px;
}

.services__photo-wrapper {
  display: grid;
  grid-template-areas: "love teenagers" "guys teenagers" "group lookbook" "group animals" "couple family" "pregnancy family" "business girls";
