}

.services__image {
  border-radius: 5px;
}

.services__link {
  position: relative;
}
.services__link--love {
  grid-area: love;
}
.services__link--teenagers {
  grid-area: teenagers;
}
.services__link--guys {
  grid-area: guys;
}
.services__link--couple {
  grid-area: couple;
}
.services__link--group {
  grid-area: group;
}
.services__link--lookbook {
  grid-area: lookbook;
}
.services__link--animals {
  grid-area: animals;
}
.services__link--family {
  grid-area: family;
}
.services__link--pregnancy {
  grid-area: pregnancy;
}
.services__link--girls {
  grid-area: girls;
}
.services__link--business {
  grid-area: business;
}

.services__title {
  position: absolute;
  left: 12px;
  bottom: 8px;
  margin: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  background: linear-gradient(180deg, rgba(25, 25, 25, 0) 53.07%, #191919 121.9%);
}

.stages-work__wrapper {
  margin-bottom: 126px;
}

.stages-work__item {
  width: 320px;
  min-height: 174px;
  border: 2px solid #E97522;
  border-radius: 12px;
  margin: 0 auto;
  margin-bottom: 40px;
  margin-left: 27px;
  margin-right: 27px;
  padding: 26px 36px 0;
  position: relative;
}
.stages-work__item::before {
  counter-increment: number;
  content: counter(number);
  font-family: Arial;
  font-size: 30px;
  font-weight: 700;
  line-height: 34px;
  letter-spacing: 0em;
  text-align: center;
  position: absolute;
  top: 134px;
  left: 132px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  color: #E97522;
  background-color: #ffffff;
  padding: 11px 19px;
}

.stages-work__description {
  margin: 0;
  text-align: center;
}

