.price__card-wrapper {
  padding: 45px 35px 35px;
}

.price__card-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.price__card-item {
  padding-bottom: 45px;
  border-radius: 16px;
}
.price__card-item:not(:last-child) {
  margin-bottom: 24px;
}
.price__card-item--mini {
