.title {
  margin: 0;
  font-size: 24px;
  line-height: 34px;
  font-weight: 600;
  color: #333333;
}
.title--h1 {
  margin-bottom: 32px;
  letter-spacing: 1.2px;
  text-align: center;
  color: #F5F5F5;
}
.title--h2 {
  line-height: 34px;
  margin-bottom: 40px;
  /* letter-spacing: 0px; */
