.swiper__wrapper {
  display: flex;
}

.swiper__button::after {
  content: "";
  background-image: url("../../img/icons/icon-arrow.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 32px;
  height: 32px;
}

.slider__button {
  border: 0;
  padding: 0;
  background: none;
  background-color: #ffffff;
  border: none;
  border-radius: 50%;
  position: absolute;
  filter: drop-shadow(0px 2px 10px rgba(0, 0, 0, 0.15));
  display: block;
}
.slider__button--prev {
  transform: translateY(-50%);
  top: 55%;
  /* left: 30px; */
}
.slider__button--next {
  transform: translateY(-50%);
  top: 55%;
  right: 130px;
  transform: rotate(180deg);
}